import axios from 'axios'

const url = '/api/persons'

const add = (newPerson) => (
    axios
    .post(url, newPerson)
    .then(response => 
        response.data
    ).catch(error => {
        console.log(`inside add ${error}`)
    })
)

const update = (id, addPerson) => (
    axios
    .put(`${url}/${id}`, addPerson)
    .then(response =>
        response.data    
    )
)

const remove = (id) => (
    axios
    .delete(`${url}/${id}`)
    .then(response =>
        response.data    
    )
)

const getAll = () => (
    axios
    .get(url)
    .then(response =>
        response.data
    )
)

export default {
    add,
    update,
    remove,
    getAll
}